                        marginBottom: '40px',
                        flexWrap: 'wrap',
                        background: 'white',
                        padding: '20px',
                        borderRadius: '16px',
                        boxShadow: '0 4px 6px -1px rgba(0, 0, 0, 0.1)',
                        maxWidth: '800px',
                        margin: '0 auto 40px'
