'use client';

import { useState } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

interface PublicHeaderProps {
    variant?: 'transparent' | 'solid';
}

export default function PublicHeader({ variant = 'solid' }: PublicHeaderProps) {
    const pathname = usePathname();
    const [isMenuOpen, setIsMenuOpen] = useState(false);

    const isTransparent = variant === 'transparent' && !isMenuOpen;

    const navLinks = [
        { href: '/about', label: '√Ä propos' },
        { href: '/docs', label: 'Documentation' },
        { href: '/careers', label: 'Recrutement' },
    ];

    return (
        <header style={{
            padding: '20px 40px',
            background: isTransparent ? 'rgba(255, 255, 255, 0.05)' : '#0f172a',
            backdropFilter: isTransparent ? 'blur(10px)' : 'none',
            position: 'fixed',
            top: 0,
            left: 0,
            right: 0,
            zIndex: 100,
            borderBottom: isTransparent ? 'none' : '1px solid rgba(255, 255, 255, 0.1)',
            transition: 'background 0.3s ease'
        }}>
            <div style={{
                display: 'flex',
                justifyContent: 'space-between',
                alignItems: 'center',
                maxWidth: '1200px',
                margin: '0 auto'
            }}>
                <Link href="/" style={{ textDecoration: 'none', color: 'white' }}>
                    <div style={{ fontSize: '24px', fontWeight: 'bold', display: 'flex', alignItems: 'center', gap: '8px' }}>
                        <div style={{
                            background: 'linear-gradient(135deg, #00c9a7, #00a389)',
                            borderRadius: '8px',
                            width: '32px',
                            height: '32px',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                            color: 'white'
                        }}>K</div>
                        KomoraLink
                        {pathname.startsWith('/docs') && (
                            <span style={{
                                fontSize: '14px',
                                opacity: 0.6,
                                marginLeft: '8px',
                                fontWeight: 'normal',
                                borderLeft: '1px solid rgba(255,255,255,0.3)',
                                paddingLeft: '10px',
                                display: 'none' // Hidden on mobile by default, handled by media query usually but inline logic here
                            }} className="hidden-mobile">Docs</span>
                        )}
                    </div>
                </Link>

                {/* Desktop Nav */}
                <nav style={{ gap: '30px', alignItems: 'center' }} className="desktop-nav">
                    {navLinks.map(link => (
                        <Link key={link.href} href={link.href} style={{
                            color: 'white',
                            textDecoration: 'none',
                            opacity: pathname.startsWith(link.href) ? 1 : 0.8,
                            fontWeight: pathname.startsWith(link.href) ? 600 : 400
                        }}>{link.label}</Link>
                    ))}
                    <Link href="/login" style={{
                        background: 'white',
                        color: '#0f172a',
                        padding: '10px 20px',
                        borderRadius: '8px',
                        textDecoration: 'none',
                        fontWeight: '600',
                        transition: 'all 0.2s',
                        marginLeft: '10px'
                    }}>
                        Connexion
                    </Link>
                </nav>

                {/* Mobile Menu Button */}
                <button
                    className="mobile-menu-btn"
                    onClick={() => setIsMenuOpen(!isMenuOpen)}
                    style={{
                        background: 'none',
                        border: 'none',
                        color: 'white',
                        fontSize: '24px',
                        cursor: 'pointer',
                        display: 'none' // Toggled via CSS usually, but we'll use a style tag for responsiveness
                    }}
                >
                    {isMenuOpen ? '‚úï' : '‚ò∞'}
                </button>
            </div>

            {/* Mobile Nav Overlay */}
            {isMenuOpen && (
                <div style={{
                    position: 'fixed',
                    top: '72px',
                    left: 0,
                    right: 0,
                    bottom: 0,
                    background: '#0f172a',
                    padding: '20px',
                    display: 'flex',
                    flexDirection: 'column',
                    gap: '20px',
                    borderTop: '1px solid rgba(255,255,255,0.1)'
                }}>
                    {navLinks.map(link => (
                        <Link
                            key={link.href}
                            href={link.href}
                            onClick={() => setIsMenuOpen(false)}
                            style={{
                                color: 'white',
                                textDecoration: 'none',
                                fontSize: '18px',
                                padding: '10px 0',
                                borderBottom: '1px solid rgba(255,255,255,0.05)'
                            }}
                        >
                            {link.label}
                        </Link>
                    ))}
                    <Link
                        href="/login"
                        onClick={() => setIsMenuOpen(false)}
                        style={{
                            background: '#00c9a7',
                            color: 'white',
                            padding: '14px',
                            borderRadius: '8px',
                            textDecoration: 'none',
                            fontWeight: '600',
                            textAlign: 'center',
                            marginTop: '20px'
                        }}
                    >
                        Connexion
                    </Link>
                </div>
            )}

            <style jsx>{`
                @media (max-width: 768px) {
                    .desktop-nav { display: none !important; }
                    .mobile-menu-btn { display: block !important; }
                    .hidden-mobile { display: none !important; }
                }
                @media (min-width: 769px) {
                    .desktop-nav { display: flex !important; }
                    .mobile-menu-btn { display: none !important; }
                    .hidden-mobile { display: inline !important; }
                }
            `}</style>
        </header>
    );
}
